.card_hero {
  background-color: #bfcdd0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 38px 0 40px 0;

  @include tablet {
    border-radius: 6px;
    width: 50%;
    min-height: calc(100vh - 215px);
  }
}

.card_reset {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  margin-right: 180px;
  background-color: #bfcdd0;
  border: 1px solid white;
  border-radius: 15px;
  text-transform: uppercase;
  font-size: 12px;
  font-family: $principal-font;
  cursor: pointer;
  color: #54585a;
  padding: 5px 10px;

  &__icon {
    padding-right: 5px;
  }
}

.card-preview {
  width: fit-content;
  width: 260px;
  height: fit-content;
  border-radius: 2px;
  background-color: white;
  text-align: center;
  padding: 21px 0 12px;
  box-shadow: 0 1px 10px 0 rgba(0, 0, 0, 0.2), 0 1px 3px 0 rgba(0, 0, 0, 0.35);

  &__header {
    border-left: 5px solid #438792;

    &--palette1 {
      border-left: 5px solid #438792;
    }

    &--palette2 {
      border-left: 5px solid #bd1010;
    }

    &--palette3 {
      border-left: 5px solid #eab052;
    }
  }

  &__photo {
    width: 100%;
    height: 250px;
    background: url('https://www.fillmurray.com/240/200');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
  }

  &__title {
    padding: 0 14px 4px 20px;
    line-height: 1.11;
    font-size: 18px;
    text-align: left;
    font-family: 'Montserrat', sans-serif;
    text-transform: capitalize;
    font-weight: bold;
    color: #114e4e;

    &--palette1 {
      color: #114e4e;
    }

    &--palette2 {
      color: #420101;
    }

    &--palette3 {
      color: #3e5b65;
    }
  }

  &__subtitle {
    padding: 0 14px 0 20px;
    margin-bottom: 10px;
    font-family: 'Montserrat', sans-serif;
    line-height: 1.25;
    font-size: 16px;
    text-align: left;
  }
}

.card__socialmedia {
  padding: 10px 0 0 20px;
  list-style-type: none;
  display: flex;
  justify-content: flex-start;
  min-height: 35px;

  &--item {
    width: 32px;
    height: 32px;
    border-radius: 15px;
    margin-right: 10px;
    padding: 6px;
    border: 2px solid #a2deaf;

    &--palette1 {
      border: 2px solid #a2deaf;
    }

    &--palette2 {
      border: 2px solid #e95626;
    }

    &--palette3 {
      border: 2px solid #a0c0cf;
    }

    .socialmedia_icon {
      color: #114e4e;

      &--palette1 {
        color: #114e4e;
      }

      &--palette2 {
        color: #420101;
      }

      &--palette3 {
        color: #3e5b65;
      }
    }
  }
}
